import { FormsModule, ReactiveFormsModule, FormControl, Validators } from '@angular/forms';
import { TextfieldComponent } from './textfield.component';
const meta = {
    title: 'Components/Textfield',
    component: TextfieldComponent,
    decorators: [
        (story) => ({
            moduleMetadata: {
                imports: [FormsModule, ReactiveFormsModule, TextfieldComponent],
            },
            template: `<div style="width: 300px;">${story()}</div>`,
        }),
    ],
    argTypes: {
        size: { control: { type: 'select', options: ['sm', 'md', 'lg'] } },
        color: { control: { type: 'select', options: ['primary', 'secondary', 'success', 'error'] } },
        value: { control: 'text' },
        placeholder: { control: 'text' },
        label: { control: 'text' },
        helperText: { control: 'text' },
        onlyNumbers: { control: 'boolean' },
        disabled: { control: 'boolean' },
        valueChange: { action: 'valueChange' }, // events
    },
};
export default meta;
// helper: create FormControl with optional validators
const createControl = (value = '', required = false) => {
    const validators = required ? [Validators.required, Validators.maxLength(10)] : [Validators.maxLength(10)];
    return new FormControl(value, validators);
};
// shared render function for story
const renderTemplate = (args) => {
    const control = createControl(args.value, args.color === 'error');
    control.valueChanges.subscribe(() => {
        console.groupCollapsed(`Field Update: ${args.label || 'Field'}`);
        console.table({
            value: control.value,
            valid: control.valid,
            invalid: control.invalid,
            touched: control.touched,
            dirty: control.dirty,
            pristine: control.pristine,
            errors: control.errors,
        });
        console.groupEnd();
    });
    return {
        props: {
            ...args,
            control,
            valueChange: args.valueChange,
        },
        template: `
      <app-textfield
        [label]="label"
        [placeholder]="placeholder"
        [size]="size"
        [color]="color"
        [helperText]="helperText"
        [disabled]="disabled"
        [onlyNumbers]="onlyNumbers"
        [control]="control"
        (valueChange)="valueChange($event)"
      ></app-textfield>
    `,
    };
};
// Stories
export const Default = {
    render: renderTemplate,
    args: {
        label: 'Username',
        placeholder: 'Enter text',
        helperText: 'Max 10 characters',
        size: 'sm',
        color: 'primary',
        value: '',
        onlyNumbers: false,
        disabled: false,
    },
};
export const WithValue = {
    render: renderTemplate,
    args: {
        ...Default.args,
        value: 'Hello',
        size: 'md',
        color: 'primary',
    },
};
export const ErrorState = {
    render: renderTemplate,
    args: {
        ...Default.args,
        value: '',
        color: 'error',
        helperText: 'This field is required',
    },
};
